* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.contenedor {
  padding-top: 80px;
  width: 100%;
  height: 100vh;
  margin-right: auto;
  margin-left: auto;
  align-items: center;
  text-align: center;
  background-color: #86b6b3;
}

.titulo {
  padding: 20px;
  text-align: center;
}

.formato {
  width: 50%;
  margin: 0px auto 80px auto;
  border: #006497 3px solid;
  border-radius: 1rem;
}

.fullentry {
  width: 50%;
  padding-top: 20px;
  text-align: left;
  margin: 0px auto;
}

.formlabel {
  margin-bottom: 0.5rem;
}

.formcontrol {
  display: block;
  width: 100%;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border-radius: 0.25rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.contenBoton {
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
  flex-direction: row;
  text-align: center;
  justify-content: space-around;
}

.boton {
  display: inline-block;
  font-weight: 400;
  line-height: 1.5;
  color: #fff;
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  background-color: #006497;
  border: 1px solid #006497;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  border-radius: 0.25rem;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
    border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.boton:hover {
  background-color: #000397;
  border: 1px solid #000397;
}

@media screen and (max-width: 701px) {
  .formato {
    width: 100%;
  }
}
